<template>
  <header ref="header" class="main" to="/home">
    <a href="#0" class="title-wrap" @click.prevent="closePage" @keyup.enter.prevent="closePage">
      <h1 class="body-appear">Daniel Carter</h1>
      <h2 class="body-appear">Develops</h2>
    </a>
    <nav>
      <ul>
        <li>
          <a href="#0" class="body-appear" @click.prevent="goToWork" @keyup.enter.prevent="goToWork" :class="{'active':workActive}">work</a>
        </li>
        <li>
          <a href="#0" class="body-appear" @click.prevent="goToContact" @keyup.enter.prevent="goToContact" :class="{'active':contactActive}">contact
          </a>
        </li>
        <li>
          <a href="https://github.com/tealicious" target="_blank">
            <app-github></app-github>
          </a>
        </li>
      </ul>
    </nav>
  </header>
</template>
<script>
import { eventBus } from "../main";
import Github from "./svg/Github.vue";
import Vue from "vue";
export default {
  data() {
    return {
      contactActive: false,
      workActive: false
    };
  },
  props: ["route"],
  methods: {
    deactivateLinks() {
      this.workActive = false;
      this.contactActive = false;
    },
    closePage() {
      this.deactivateLinks();
      eventBus.$emit("closePage");
    },
    goToWork() {
      this.deactivateLinks();
      eventBus.$emit("goToWork");
      this.workActive = true;
    },
    goToContact() {
      this.deactivateLinks();
      eventBus.$emit("goToContact");
      this.contactActive = true;
    }
  },
  components: {
    appGithub: Github
  },
  mounted() {
    console.log(this.route);
    if (this.route == "work") {
      this.workActive = true;
    }
    if (this.route == "contact") {
      this.contactActive = true;
    }
  }
};
</script>


