<template>
  <div id="app">
    <app-header :route="route"></app-header>
    <div class=" page ">
      <div class="pagewrap ">
        <transition :name="transition" mode="out-in">
          <router-view></router-view>
        </transition>
      </div>
    </div>
    <app-cover-image :pageOpen="pageOpen " v-if="drawComponent "></app-cover-image>
    <app-circles :pageOpen="pageOpen " v-if="userHasNavigated "></app-circles>
  </div>
</template>

<script>
import Vue from "vue";
import CoverImage from "./components/CoverImage.vue";
import Circles from "./components/Circles.vue";
import Header from "./components/Header.vue";
import { eventBus } from "./main";
const body = document.getElementsByTagName("BODY")[0];
export default {
  name: "app",
  data() {
    return {
      transition: "slide-fade",
      pageOpen: false,
      drawComponent: true,
      invertColors: false,
      navigated: false,
      route: "home",
      routes: [
        {
          to: "/",
          name: ""
        },
        {
          to: "/work",
          name: "work"
        },
        {
          to: "/contact",
          name: "contact"
        }
      ]
    };
  },
  computed: {
    userHasNavigated() {
      this.drawComponent;
      this.navigated;
      return this.drawComponent && this.navigated;
    }
  },
  methods: {
    bodyColorSwap() {
      if (this.invertColors) {
        body.classList.remove("invert");
      } else {
        body.classList.add("invert");
      }
      this.invertColors = !this.invertColors;
    },
    reAnimate() {
      this.drawComponent = false;
      Vue.nextTick(() => {
        this.drawComponent = true;
      });
    },
    openPage() {
      this.navigated = true;
      if (!this.pageOpen) {
        this.pageOpen = true;
        this.reAnimate();
      }
    },
    closePage() {
      if (this.pageOpen) {
        this.pageOpen = false;
        this.reAnimate();
        this.$router.push("/");
      }
    },
    goToWork() {
      this.openPage();
      this.$router.push("/work");
    },
    goToContact() {
      this.openPage();
      this.$router.push("/contact");
    }
  },
  components: {
    appCoverImage: CoverImage,
    appCircles: Circles,
    appHeader: Header
  },
  created() {
    this.route = this.$route.name;
    if (this.$route.name != "home") {
      this.pageOpen = true;
      this.navigated = true;
    }
    eventBus.$on("goToWork", this.goToWork);
    eventBus.$on("goToContact", this.goToContact);
    eventBus.$on("closePage", this.closePage);
  }
};
</script>
<style>

</style>


